
<template id="Maldi">
  <div class="post-box">
    <span class="post-views">{{ views }}</span>
    <h3 class="post-title" v-globalhighlight="'yellow'">{{ title }}</h3>
    <span class="post-date">{{ date }}</span>
    <p class="post-content">{{ content }}</p>
    <div class="row">
      <div class="col-sm-6">
        <span class="post-author">{{author }}</span>
      </div>
      <div class="col-sm-6">
        <span class="post-category">{{ category }}</span>
      </div>
    </div>
  </div>
  
</template>

<script>
export default {
  props: ["views", "title", "date", "content", "author", "category"],
  name: "post"
};
</script>
<style scoped>
.post-box {
  background-color: white;
  text-align: left;
  margin-bottom: 20px;
}
.post-views {
  float: right;
  background-color: violet;
  padding: 6px 10px;
  border-radius: 4px;
}
.post-title,
.post-date,
.post-content {
  text-align: left;
  padding: 10px;
}
.post-author {
  float: left;
  background-color: plum;
  padding: 6px 10px;
  border-radius: 4px;
}
.post-category {
  float: right;
  background-color: aquamarine;
  padding: 6px 10px;
  border-radius: 4px;
}
</style>
